.events {
  &__title {
    margin-bottom: 40px;

    @include onTablet {
      margin-bottom: 48px;
    }

    @include onDesktop {
      margin-bottom: 56px;
    }
  }

  &__exhibition {
    margin-bottom: 60px;

    @include onTablet {
      margin-bottom: 64px;
    }

    @include onDesktop {
      margin-bottom: 104px;
    }
  }

  & > :nth-child(3) {
    @include onTablet {
      margin-bottom: 112px;
    }
  }
}
